<template>
  <div id="app">
    <div class="da">
         <img src="./assets/2.jpg" class='img2'>
         <img ref="imgO" src="./assets/1.jpg" class='img1'>
         <div class="plate">粤B43081D</div>
         <div class="date">{{now_time}}</div>
         <div class="nowTime">{{nowTime}}</div>
    </div>

  </div>
</template>

<script>
function getStyle (obj, name) {
  return obj.currentStyle ? obj.currentStyle[name] : window.getComputedStyle(obj, null)[name]
}
let a = 1
let b
function startMove (obj, json, fnEnd) {
  clearInterval(obj.timer)
  obj.timer = setInterval(function () {
    var bStop = true
    for (var attr in json) {
      var cur = (attr === 'opacity') ? Math.round(parseFloat(getStyle(obj, attr)) * 100) : parseInt(getStyle(obj, attr))
      var speed = (json[attr] - cur) / 40
      speed = speed > 0 ? 2 : -2
      if (Math.abs(cur - json[attr]) > 2) bStop = false
      if (attr === 'opacity') {
        obj.style.filter = 'alpha(opacity=' + (cur + speed) + ')'
        obj.style.opacity = (cur + speed) / 100
      } else {
        obj.style[attr] = cur + speed + 'px'
      }
    }
    if (bStop) {
      clearInterval(obj.timer)
      a = a === 1 ? 0 : 1
      startMove(b, a === 1 ? { left: 200 } : { left: 35 })
    }
  }, 30)
}

export default {
  name: 'App',
  data () {
    return {
      now_time: '',
      nowTime: ''
    }
  },
  methods: {
    deteT () {
      var now = new Date()
      var year = now.getFullYear()
      var month = now.getMonth()
      var date = now.getDate()

      month = month + 1
      month = month < 10 ? '0' + month : month

      this.now_time = year + '年' + month + '月' + date + '日'
    },
    nowTimeF () {
      var now = new Date()
      var hour = now.getHours()
      var minutes = now.getMinutes()
      hour = hour < 10 ? '0' + hour : hour
      minutes = minutes < 10 ? '0' + minutes : minutes
      this.nowTime = hour + ':' + minutes
    }
  },
  mounted () {
    b = this.$refs.imgO
    startMove(this.$refs.imgO, { left: 200 })
    this.deteT()
    setInterval(() => {
      this.nowTimeF()
    }, 1000)
  }
}

</script>

<style>
.da{
  position: relative;
}
.img2{
  width: 100%;
}
.img1{
  width:calc(30% + 5px);
 position: absolute;
 top:calc(50% - 15px) ;
 left: 30px;
}
.plate{
position: absolute;
top:  calc(13% + 5px);
 left:  calc(30% + 5px );
 font-size: 13px;
 color: #8F8F8F;
}
.date{
  position: absolute;
top:  calc(18% - 5px);
 left:  calc(8% + 3px);
 font-size: 15px;
 color: #222222;
}
.nowTime{
  position: absolute;
top:  calc(2% - 8px);
 left:  calc(90% - 10px);
 font-size: 10px;
 color: #707070;
}
</style>



